import React, { Component } from 'react';

import ReactDOM from 'react-dom';

import styled from 'styled-components';
import { BREAKPOINT_LG,BUTTON_BOX_SHADOW,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM} from '../types';

const StyledToTop= styled.div`

position: fixed;
    left: 30px;
    cursor: pointer;
    bottom: 30px;
    width: 45px;
  
   
    box-shadow:0 0 10px 5px rgba(0,0,0,0.1);
    visibility:hidden;
    z-index:999;
    transition: 0.3s ease;
    height: 45px;
    &.active{
      visibility:visible;
    }
    background: #fff;
  
    border-radius: 50%;
    align-items: center;
    display: flex;
    justify-content: center;
   
&:active{
  box-shadow:0 0 10px 5px rgba(0,0,0,0.1),  ${BUTTON_BOX_SHADOW};
}
  i{
    color: ${FONT_COLOR};
    font-size:24px;
  
}
`;
class ToTop extends Component {

    constructor(props) {
        super(props);

        this.state = {
            is_Mounted:false,

            };
this.top = React.createRef()
             this.handleClick = this.handleClick.bind(this);
this.handleScroll = this.handleScroll.bind(this);
    }

    handleClick(){

      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      })

  }

 handleScroll() {

var node = ReactDOM.findDOMNode(this.refs.top);

        var scroll_class = "active";

        if (window.pageYOffset >600) {
            node.classList.add(scroll_class);

        } else {
            node.classList.remove(scroll_class);
        }


  }
  componentDidMount(){
    window.addEventListener("scroll", this.handleScroll);

  }
   componentWillUnmount() {
        window.removeEventListener("scroll", this.handleScroll);

  }
   render() {
    return (
      <StyledToTop dark={this.props.dark} side={this.props.side} ref="top" onClick = {this.handleClick}>
      <i className="bx bx-chevron-up"></i>
  </StyledToTop>
    );
  }


};
export default ToTop;
